html {
  height: 100%;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*, *::before, *::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

#root {
  height: 100%;
  width: 100%;
  overflow:auto;
}

body {
  margin: 0;
  font-family: 'Proxima Nova', sans-serif;
  height: 100%;
  width: 100%;
  background-color: black;
}

.hacker {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.homepage {
  /* background-color: black; */
  /* background-image: url("./img/hacker.gif"); */
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 10;
  overflow:auto;
}

.globe {
  height: 100%;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.form {
  margin-top: 45vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  z-index: 10;
}

.formControl {
  border: 1px solid #0ed3cd;
  background-color: rgba(0, 0, 0, 0.25);
  font-family: 'Proxima Nova', sans-serif;
  color: white;
  padding: 12px 20px;
  margin: 8px 25px;
  width: 250px;
  border-radius: 8px;
  font-size: 11px;
  letter-spacing: 2px;
  z-index: 10;
}

.formControl:focus {
  outline: none;
}

.formColor {
  border-color: red;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.form-errors {
  color: white;
}

.btn {
  background-color: #0ed3cd;
  color: white;
  padding: 12px 0;
  margin: 20px;
  font-family: 'Proxima Nova', sans-serif;
  width: 150px;
  border-radius: 5px;
  letter-spacing: 2px;
  border: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-decoration: none;
  font-size: 12px;
  z-index: 10;
}

.btn:disabled {
  opacity: 0.5;
}

.btn:focus{
  outline: none
}

.btn:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.logo {
  height: 45px;
  -o-object-fit: contain;
     object-fit: contain;
}

.logo-container {
  position: fixed;
  top: 60px;
  left: 60px;
  z-index: 10;
}

/* bottom */

.fadeOut{
     opacity:0 !important;
     /* width:0;
     height:0; */
     -webkit-transition: opacity 0.5s;
     -o-transition: opacity 0.5s;
     transition: opacity 0.5s;

}
.fadeIn{
     opacity:1;
     /* width:100px;
     height:100px; */
     -webkit-transition: all 0.5s;
     -o-transition: all 0.5s;
     transition: all 0.5s;

}

.hidden {
  background-color: #333333;
  color: #333;
  border-color: #333;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.hidden::-webkit-input-placeholder {
  color: #333;

}

.hidden:-ms-input-placeholder {
  color: #333;

}

.hidden::-ms-input-placeholder {
  color: #333;

}

.hidden::placeholder {
  color: #333;

}

.disabled {
  opacity: 0.5;
  -webkit-filter: brightness(0.5);
          filter: brightness(0.5);
  pointer-events: none;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.aboutDiv a{
  color: white;
  text-decoration: none;
  font-size: 18px;
    font-family: 'Proxima Nova', sans-serif;
    letter-spacing: 3px;
    position: absolute;
    bottom: 25px;
    right: 40px;
}
.aboutDiv a:hover{
  text-decoration: none;
}



