#root {
  height: 100%;
  width: 100%;
}

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*, *::before, *::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

.signup {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
}

.signup-title {
  font-size: 30px;
  font-weight: 600;
  letter-spacing: 4px;
  color: black;
  margin: 30px;
}

.signup-formControl {
  background-color: transparent;
  border: none;
  border-bottom: 2px solid gray;
  font-family: 'Lato', sans-serif;
  padding: 5px;
  margin: 15px 10px;
  width: 100%;
  height: 30px;
  font-size: 12px;
  font-weight: 300;
}

.signup-formControl:focus {
  outline: none;
  border-bottom: 2px solid black;
}

.signup-form {
  width: 500px;
  height: 525px;
  overflow: scroll;
  padding: 30px;
  background-color: white;
  /* display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; */
  margin: 0;
  border-radius: 20px;
  margin-top: -50px;
  text-align: center;
}

.form-inner {
  margin: auto;
  width: 80%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-direction: column;
      flex-direction: column;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
}

.email {
  width: 90%;
}

.nationality {
  width: 70%;
}

.signup-btn {
  color: gray;
  margin-top: 40px;
}

.signup-btn:hover {
  color: black
}

.signup-title2 {
  font-size: 14px;
}

.datepicker {
  background-color: transparent;
  border: none;
  border-bottom: 2px solid gray;
  font-family: 'Lato', sans-serif;
}

.signup-formControl:focus {
  outline: none;
  border-bottom: 2px solid black;
}

.country {
  width: 200px;
  margin: 30px 0;
  margin-right: 40px;
  background-color: #f5a99b;
  border: none;
  font-family: 'Lato', sans-serif;
  height: 20px;
}

.country:focus, .region:focus {
  outline: none
}

.region {
  width: 200px;
  margin: 30px 0;
  background-color: #8fbdbb;
  border: none;
  font-family: 'Lato', sans-serif;
  height: 20px;
}

.datepicker {
  width: 250px;
  text-align: center;
}



